@import "color";

.video {width: 100%; background-color: #666;}
.curriculum-list-wrap {
    .tlt-wrap {
        .tlt {font-size: 1.2rem; padding: .5rem 0; margin-bottom: 1rem; font-weight: 700;}
    }
    .curriculum-list {
        .curriculum-item {padding: .5rem 0; display: flex; align-items: center; justify-content: space-between; cursor: pointer;
            &:not(:last-child) {border-bottom: 1px solid #e7e7e8;}
            &.active {font-weight: 700;}
            .video-duration {font-size: .875rem; color: #888;}
        }
    }
}
.curriculum-info-wrap {padding: 1rem; border: 1px solid #e7e7e8; border-radius: .375rem;
    .tlt {font-size: 1.2rem;}
    .curriculum-info {
        p {color: #888;}
    }
}